<template>
  <el-row class="mainWrapper loginWrapper" :gutter="10">
    <el-col :span="12" class="logo">
      <el-image style="width: 100px; height: 100px">
        <div slot="error" class="image-slot">
          <i class="el-icon-picture-outline"></i>
        </div>
      </el-image>
    </el-col>
    <el-col :span="12" class="loginForm">
      <el-card>
        <div class="login_title">{{ $t("login.loginTitle") }}</div>
        <lang-icon></lang-icon>
        <el-form ref="form" :model="form" :rules="rules">
          <el-form-item prop="email" :error="nameErrMsg">
            <el-input
              v-model="form.email"
              :placeholder="$t('login.emailPlaceholder')"
              prefix-icon="el-icon-user"
            >
            </el-input>
          </el-form-item>
          <el-form-item prop="password" :error="errMsg">
            <el-input
              v-model="form.password"
              :type="passwordStatus"
              prefix-icon="el-icon-lock"
              :placeholder="$t('login.passwordPlaceholder')"
            ></el-input>
            <span class="psw_icon" @click="changePswStatus"
              ><i
                class="iconfont"
                :class="
                  passwordStatus === 'password' ? 'icon-eye' : 'icon-eye1'
                "
              ></i
            ></span>
          </el-form-item>
        </el-form>
        <el-row :gutter="10">
          <el-col :span="12">
            <el-checkbox v-model="form.status">
              <p class="text">{{ $t("login.remember") }}</p>
            </el-checkbox>
          </el-col>
        </el-row>
        <el-button
          type="primary"
          :loading="isLogin"
          class="loginBtn"
          @click="submitForm"
          >{{ $t("btn.login") }}</el-button
        >
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
import { userAPI } from "@/api";
import LangIcon from "@/components/LangIcon";
export default {
  // mounted() {
  //   userAPI.getAllShop();
  //   this.getAllUserInfo();
  // },

  components: {
    LangIcon,
  },
  data() {
    return {
      form: {
        email: "",
        password: "",
        status: false,
      },
      errMsg: "",
      nameErrMsg: "",
      isLogin: false,
      passwordStatus: "password",
    };
  },

  methods: {
    submitForm() {
      // this.$refs["form"].clearValidate();
      this.$refs["form"].validate((valid) => {
        if (valid) {
          this.isLogin = true;
          const data = {
            ...this.form,
            grant_type: "password",
          };
          // this.$router.push('/com');
          this.$store.dispatch("user/login", data).then(
            () => {
              this.isLogin = false;
            },
            (err) => {
              if (err) {
                this.errMsg = "";
                this.nameErrMsg = "";
                this.$nextTick(() => {
                  console.log(err);
                  if (err.status === 401) {
                    this.isLogin = false;
                    this.errMsg = this.$t("login.userNameErrMsg");
                    this.nameErrMsg = " ";
                  } else {
                    this.isLogin = false;
                    this.errMsg = this.$t("error.serverErr");
                    this.nameErrMsg = " ";
                  }
                });
              }
            }
          );
        } else {
          return false;
        }
      });
    },
    changePswStatus() {
      if (this.passwordStatus === "password") {
        this.passwordStatus = "";
      } else {
        this.passwordStatus = "password";
      }
    },
    getAllUserInfo() {
      const data = {
        operate_mode: "get_login_list",
      };
      userAPI.loginOperate(data);
    },
    async forgetPassword() {
      let email = this.$store.state.user.loginData.email;
      await userAPI.forgetPassword({
        email: email,
      });
      let obj = {
        email: this.$store.state.user.loginData.email,
        password: this.target.value,
      };
      await userAPI.forgetPassword2(obj);
    },
  },
  // watch: {
  //   newForm: {
  //     handler: function (val, oldVal) {
  //       console.log(val, oldVal);
  //       if (this.$Cookies.get("token")) {
  //         this.showEye = val.email.trim() === this.$Cookies.get("login_email") && val.password.trim() === "fake" ? false : true;
  //         return;
  //       }
  //       if (!(val.email.trim() === oldVal.email && val.password.trim() === oldVal.password)) {
  //         this.showEye = true;
  //       }
  //     },
  //     deep: true,
  //   },
  // },
  computed: {
    rules() {
      return {
        email: [
          {
            required: true,
            message: this.$t("login.emailErrMsg"),
            trigger: "blur",
          },
        ],
        password: [
          {
            required: true,
            message: this.$t("login.pswErrMsg"),
            trigger: "blur",
          },
        ],
      };
    },
  },
};
</script>

