.mainWrapper {
  height: 100%;
  width: 100%;
}

.login_title {
  display: inline-block;
  width: 100%;
  margin-left: -30px;
  text-align: center;
  padding-bottom: 1em;
}

.text {
  font-size: 12px;
}

.psw_icon {
  position: absolute;
  height: 100%;
  right: 5px;
  top: 0;
  text-align: center;
  color: #c0c4cc;
  transition: all 0.3s;
  cursor: pointer;
  // pointer-events: none;
}
.loginBtn {
  margin-left: 10%;
  margin-top: 10%;
  width: 80%;
}
</style>
